

























import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class ErrorModal extends Vue {
    /**
     * Payload, passed when opening the modal.
     */
    @Prop()
    payload: {
        title: string;
        message: string;
    };

    /**
     * Close the modal.
     */
    close() {
        this.$store.dispatch("modal/close");
    }
}
